<template>
  <div class="bg-white w-100">
    <div class="content mx-auto d-flex">
      <div class="logo">
        <img
          src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png"
          alt="美团"
        />
      </div>
      <div class="search">
        <div class="search-input">
          <input
            @input="fetchSearch"
            @focus="isFocus = true"
            @blur="isFocus = false"
            v-model="inputValue"
            placeholder="搜索商家与地点"
            type="text"
          />
          <button class="btn search-btn text-white">
            <i class="el-icon-search"></i>
          </button>
          <ul
            class="position-absolute search-box"
            v-show="isFocus && searchList.length > 0"
          >
            <li v-for="(item,i) in searchList" :key="i">
              <router-link :to="`/index#${item}`">{{ item }}</router-link>
            </li>
          </ul>
        </div>
        <div class="hot-word">
          <router-link
            v-for="item in hotList"
            :key="item"
            :to="`/index/s#${item}`"
            >{{ item }}</router-link
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      inputValue: "",
      hotList: [
        "故宫博物院",
        "北京欢乐谷",
        "故宫珍宝馆",
        "尚隐·泉都市生活馆",
        "八达岭长城",
        "北京连升商务酒店",
      ],
      searchList: [],
      isFocus: false,
    };
  },
  methods: {
    async fetchSearch() {
      if (this.inputValue.trim() != "") {
        // 节流操作
        let res = await this.$api.fetchSearch();
        console.log(res);
        this.searchList = res.data.list;
      } else {
        this.searchList = [];
      }
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/css/common/var.less";
.content {
  width: @width;
  padding-bottom: 35px;
  padding: 20px;
  .logo {
    width: 280px;
    text-align: center;
    img {
      margin-top: 15px;
      width: 126px;
      height: 46px;
    }
  }
  .search {
    .hot-word {
      padding-left: 16px;
      line-height: 1;
      margin-top: 8px;
      height: 14px;
      a {
        color: #999;
        margin-right: 10px;
        margin-bottom: 3px;
        display: inline-block;
        font-size: 12px;
        &:hover {
          color: @green;
        }
      }
    }
    padding-top: 16px;
    .search-input {
      input {
        width: 462px;
        height: 40px;
        line-height: 40px;
        border: 1px solid @green;
        padding: 0 15px;
        color: #606266;
        border-radius: 4px;
        border-top-right-radius: 0;
        border-right: none;
        border-bottom-right-radius: 0;
        &::placeholder {
          color: #c0c4cc;
        }
      }
      .search-btn {
        width: 88px;
        height: 40px;
        transform: translateY(-2px);
        background-color: @green;
        border-radius: 0 4px 4px 0;
      }
    }
    .search-box {
      border: 1px solid #e5e5e5;
      border-top: none;
      background: #fff;
      z-index: 999;
      box-shadow: 0 3px 5px 0 rgb(0 0 0 / 10%);
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
      width: 462px;
      font-size: 12px;
      text-align: left;
      li {
        &:hover {
          background: #f8f8f8;
        }
        a {
          padding: 3px 10px;
          color: #333;
          line-height: 1.6;
        }
        &:hover a { 
          color: #fe8c00;
        }
      }
    }
  }
}
</style>